import React from 'react';

import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      searchResults: {
        query: '',
        items: []
      }
    };

    this.handleSearch = this.handleSearch.bind(this);
  }

  componentDidMount() {
    fetch('/data.json')
      .then(res => res.json())
      .then(data => {
        data = data.reverse();

        const searchResults = {
          ...this.state.searchResults,
          items: data
        };

        this.setState({ data, searchResults });
      });
  }

  handleSearch(e) {
    const query = e.target.value.toLowerCase();

    if (query === '') return this.setState({ searchResults: { query, items: this.state.data } });

    const items = this.state.data.filter(item => {
      return (
        item.name.toLowerCase().indexOf(query) >= 0 ||
        item.brewery.toLowerCase().indexOf(query) >= 0 ||
        item.style.toLowerCase().indexOf(query) >= 0 ||
        Number(query) === item.rating
      );
    });

    this.setState({ searchResults: { query, items } });
  }

  render() {
    return (
      <div className="wrapper">
        <div className="logo">
          <p>
            b23r2p23r<span>{this.state.data.length}</span>
          </p>
        </div>
        <div className="beer-search">
          <input
            type="text"
            value={this.state.searchResults.query}
            placeholder="–ü–æ–∏—Å–∫ –ø–∏–≤–∞—Å–∏–∫–∞ üç∫"
            onChange={this.handleSearch}
          />
        </div>
        <div className="beer-list">
          {!this.state.searchResults.items.length && (
            <div className="beer-list-item">
              <div className="beer-list-item-description">
                <p className="beer-list-item-description-title">–ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ</p>
              </div>
            </div>
          )}
          {this.state.searchResults.items.map((beer, beerIndex) => (
            <div
              key={beerIndex}
              className="beer-list-item"
              style={{
                backgroundImage: `linear-gradient(to right, rgba(255, 200, 118, 0.2), rgba(255, 200, 118, 0.2) ${beer.rating *
                  10}%, #fff ${beer.rating * 10}%)`
              }}
            >
              <div className="beer-list-item-description">
                <p className="beer-list-item-description-title">{beer.name}</p>
                <p className="beer-list-item-description-brewery">{beer.brewery}</p>
                <p className="beer-list-item-description-style">{beer.style}</p>
              </div>
              <div className="beer-list-item-rate">
                <p className="beer-list-item-rate-r">
                  <sup>{beer.rating}</sup>&frasl;<sub>10</sub>
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default App;
